import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import folders from '../../folders';

function Evolution() {
  const [projectInfo, setProjectInfo] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async (folder) => {
      const home = window.location;
      const url = home + `/Sheets/${folder}/data.json`;

      try {
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        return data.data[0];
      } catch (error) {
        console.error(`Error fetching data for folder: ${folder}`, error);
        return null;
      }
    };

    const fetchAllData = async () => {
      const allData = await Promise.all(folders.map(folder => fetchData(folder)));
      const filteredData = allData.filter(data => data.project_title != 'empty');
      const sortedData = filteredData.sort((a, b) => a.id - b.id);
      setProjectInfo(sortedData);
    };

    fetchAllData();
  }, []);

  return (
    <div className='bg-base-100 mt-5 rounded-lg'>
      <div className='max-w-6xl mx-auto py-20 px-5 2xl:px-14'>

        <div className='px-5 2xl:px-0'>
          <h1 className='text-2xl font-bold'>Evaluation Status</h1>
          <p className='text-lg pt-2 text-gray-600'>The table below provides an overview of the evaluation sheet statuses with their date of upload. 😊</p>
                <a
  target='_blank'
  href='https://github.com/rphlr/42-Evals'
  className='group flex w-full items-center text-blue-500 rounded-lg hover:text-blue-100'
>
  <span className='underline'>Big thanks to rphlr for the base</span>
</a>
          <div className="overflow-x-auto">
            <table className='w-full mt-4 table table-auto'>
              <thead>
                <tr className='bg-base-300 text-base-content text-center'>
                  <th className='py-3 text-center px-5 font-semibold w-1/12 text-lg'>PROJECTS 📂</th>
                  <th className='py-3 text-center px-5 font-semibold w-1/12 text-lg'>DURATION ⌛</th>
                  <th className='py-3 text-center px-5 font-semibold w-1/12 text-lg'>COST 🎯</th>
                  <th className='py-3 text-center px-5 font-semibold w-1/12 text-lg'>EDIT✍🏻</th>
                </tr>
              </thead>
              <tbody className='text-center bg-base-200'>
                {projectInfo.filter(project => project.status === 'active').map((project, index) => (
                  <tr key={index} className='border-b border-gray-200'>
                    <td className='py-3 px-5 text-base-content cursor-pointer'>
                      <div
                        className="flex justify-center items-center gap-2 text-xl text-[#0D94B6]"
                        onClick={() => navigate(`/sheets?project=${project.project_title}`)}
                      >
                        {project.project_title}
                      </div>
                      <span className='text-lg text-base-content text-bold'>
                      {new Date(project.updated_at).toLocaleDateString('en-GB', {
                        day: 'numeric',
                        month: 'numeric',
                        year: 'numeric'
                      })}
                      </span>
                    </td>
                    <td className='py-3 px-5 w-1/12'>
                      <span className='text-base-content font-bold text-xl'>
                      {project.time}'
                      </span>
                    </td>
                    <td className='py-3 px-5 w-1/12'>
                      <span className='text-base-content font-bold text-xl'>
                      {project.eval_points}
                      </span>
                    </td>
                    <td className='py-3 px-5 w-1/12'>
                      <div
                        className="flex justify-center items-center gap-2 text-sm text-blue-500 cursor-pointer"
                        onClick={() => navigate(`/edit?project=${project.project_title}`)}
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth="1.5" stroke="currentColor" className="size-8"><path strokeLinecap="round" strokeLinejoin="round" d="m16.862 4.487 1.687-1.688a1.875 1.875 0 1 1 2.652 2.652L10.582 16.07a4.5 4.5 0 0 1-1.897 1.13L6 18l.8-2.685a4.5 4.5 0 0 1 1.13-1.897l8.932-8.931Zm0 0L19.5 7.125M18 14v4.75A2.25 2.25 0 0 1 15.75 21H5.25A2.25 2.25 0 0 1 3 18.75V8.25A2.25 2.25 0 0 1 5.25 6H10"></path></svg>
                      </div>
                    </td>
                    
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Evolution;